// Bibliothques
import React from "react";
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";

/**
 * Le rle de ce composant est d'afficher une bote de dialogue de confirmation
 * avec un message de confirmation et deux boutons, "Annuler" et "Valider".
 * Il est utilis pour demander une confirmation  l'utilisateur avant d'effectuer une action.
 * Dans PEPH, il est utilis uniquement pour les suppressions.
 */
function FormConfirmation({ isOpen, toggle, onSave }) {
  return (
    <Modal isOpen={isOpen} toggle={toggle}>
      <ModalHeader>Confirmation</ModalHeader>
      <ModalBody>Etes-vous sr de vouloir supprimer cet lment ?</ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={toggle}>
          Annuler
        </Button>
        <Button className="ml-auto" color="success" onClick={onSave}>
          Valider
        </Button>
      </ModalFooter>
    </Modal>
  );
}

export default FormConfirmation;
